<ul>
  <li>Class component structure allows me to use component lifecycles and states, which may come in handy when I want to write blog posts about topics such as AI where I'd demonstrate the AI model that I'd host in a backend server somewhere and the post will be the client app that interacts with it.</li>
  <li>The title and the date of the post are passed as props to the component at the index page, so the post has access to them to use for setting the document.title and post headers.</li>
  <li>I use componentDidMount() to set the document title.</li>
  <li>For convenience in writing, I reset the indentation to the left in the render.</li>
  <li>There is a directory called <strong>codes</strong> with all the code blocks I want to handle with code-splitting.</li>
</ul>
<p>
